import React,{ useState,useEffect} from 'react'
import PropTypes from 'prop-types'
import { connect } from 'react-redux'
import {Avatar, Button, Card, Image, Table, Tooltip, Typography} from 'antd'
import { CheckOutlined,CloseOutlined } from '@ant-design/icons'
import {_GetMyRoomQueue} from "../../../../redux/actions/rooms/room-queue";
import Utils from "../../../../utils";
import {first} from "lodash";
import {Link} from "react-router-dom";
import moment from "moment";
import RoomsQueueHistory from "./rooms-queue-history";

const RoomsQueueViews = (props)=> {
    let { _GetMyRoomQueue } = props

    useEffect(()=> {
        _GetMyRoomQueue({
            page:1,
            limit:10,
            status:['waiting'],
            withUser:true,
            direction:"desc",
            orderBy:"book_date"

        })
    },[_GetMyRoomQueue])


    const columns = [
        {
            title:"No",
            width:50,
            render: (_,val,index)=> index + 1
        },
        {
            title:"Room",
            dataIndex: "room",
            width:80,
            render: (_,val,index)=> {
                return (
                    <div className="w-full">
                        <Avatar
                            size={60}
                            src={
                                _?.images && Array.isArray(_?.images) && _?.images.length > 0 ?(
                                    <Image
                                        style={{
                                            fontSize:"10px !important"
                                        }}
                                        width={60}
                                        height={60}
                                        fallback={'/img/image-notfound.png'}
                                        src={Utils.ImagePath(first(_?.images)?.prefix_url,first(_?.images)?.filename,'/')}
                                    />
                                    )
                                    :null
                        }
                        />
                    </div>
                )
            }
        },
        {
            title:"Room Name",
            dataIndex:"room",
            render: (_,val)=> {
                return (
                    <div className="w-full truncate" style={{width:120}}>
                        <Link to={'/'}>
                            {_?.name ?? "-"}
                        </Link>
                    </div>
                )
            }
        },
        {
            title:"Date",
            dataIndex: "book_date",
            render: (_)=> {
                return(
                    <Typography>{_ ? moment(_).format("DD MMMM YYYY") : "-"}</Typography>
                )
            }
        },
        {
            title:"Duration",
            dataIndex: "room",
            render: (_)=> {
                return(
                    <Typography>{[1,_?.unit].join(" ") ?? "-"}</Typography>
                )
            }
        },
        {
            dataIndex: "user",
            render:(_,val)=>{
                return (
                    <div className="w-full">
                        {_?.email ?? "-"}
                    </div>
                )
            }
        },
        {
            key:"action",
            render: (_,val,index)=> {
                return(
                    <div className="text-right">
                        <Tooltip title={'Confirm Book Room'}>
                            <Button shape={'circle'} type={'primary'} ghost size={'small'} icon={<CheckOutlined />} className={'mr-2'}/>
                        </Tooltip>
                        <Tooltip title={'Reject Book Room'}>
                            <Button shape={'circle'} danger ghost size={'small'} icon={<CloseOutlined />}/>
                        </Tooltip>
                    </div>
                )
            }
        }
    ]

    return (
        <div className="w-full">
            <Card title={'Queue'}>
                <Table
                    loading={props?.loading ?? false}
                    dataSource={props?.data ?? [ ]}
                    columns={columns}
                />
            </Card>

            <div className="w-full">
                <RoomsQueueHistory/>

            </div>
        </div>
    )
}

RoomsQueueViews.propTypes = {
    _GetMyRoomQueue: PropTypes.func.isRequired
}
RoomsQueueViews.defaultProps = {
    _GetMyRoomQueue: (params = {})=> {
        console.log({params})
    }
}

export default connect(({Room})=> {
    let { queue } = Room
    let { waiting } = queue
    return { ...waiting }
},{_GetMyRoomQueue})(React.memo(RoomsQueueViews))