import React, {useEffect, useState} from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import {_GetMyRoomQueueLists} from "../../../../../redux/actions/rooms/room-queue";
import {Avatar, Button, Card, Image, Table, Tag, Tooltip, Typography} from "antd";
import Utils from "../../../../../utils";
import {first} from "lodash";
import {Link} from "react-router-dom";
import moment from "moment/moment";
import {CheckOutlined, CloseOutlined} from "@ant-design/icons";

const RoomsQueueHistory = (props) => {
    let {_GetMyRoomQueueLists} = props

    useEffect(() => {
        _GetMyRoomQueueLists({
            page: 1,
            limit: 10,
            status: ["rejected", "accepted"],
            withUser: true
        })
    }, [_GetMyRoomQueueLists])

    const columns = [
        {
            title: "No",
            width: 50,
            render: (_, val, index) => index + 1
        },
        {
            title: "Room",
            dataIndex: "room",
            width: 80,
            render: (_, val, index) => {
                return (
                    <div className="w-full">
                        <Avatar
                            size={60}
                            src={
                                _?.images && Array.isArray(_?.images) && _?.images.length > 0 ? (
                                        <Image
                                            style={{
                                                fontSize: "10px !important"
                                            }}
                                            width={60}
                                            height={60}
                                            fallback={'/img/image-notfound.png'}
                                            src={Utils.ImagePath(first(_?.images)?.prefix_url, first(_?.images)?.filename, '/')}
                                        />
                                    )
                                    : null
                            }
                        />
                    </div>
                )
            }
        },
        {
            title: "Room Name",
            dataIndex: "room",
            render: (_, val) => {
                return (
                    <div className="w-full truncate" style={{width: 120}}>
                        <Link to={'/'}>
                            {_?.name ?? "-"}
                        </Link>
                    </div>
                )
            }
        },
        {
            title: "Date",
            dataIndex: "book_date",
            render: (_) => {
                return (
                    <Typography>{_ ? moment(_).format("DD MMMM YYYY") : "-"}</Typography>
                )
            }
        },
        {
            title: "Duration",
            dataIndex: "room",
            render: (_) => {
                return (
                    <Typography>{[1, _?.unit].join(" ") ?? "-"}</Typography>
                )
            }
        },
        {
            dataIndex: "user",
            render: (_, val) => {
                return (
                    <div className="w-full">
                        {_?.email ?? "-"}
                    </div>
                )
            }
        },
        {
            key: "action",
            render: (_, val, index) => {
                return (
                    <div className="text-right">
                        {
                            _?.status === "waiting" ? null :
                                _?.status === "accepted" ? (
                                    <Tag color={'cyan'}>Accepted</Tag>
                                ) : (
                                    <Tag color={'red'}>Rejected</Tag>

                                )

                        }

                    </div>
                )
            }
        }
    ]

    return (
        <Card title={'History'} className="w-full">
            <Table
                loading={props?.loading ?? false}
                dataSource={props?.data ?? []}
                columns={columns}
            />
        </Card>
    )
}

RoomsQueueHistory.propTypes = {
    _GetMyRoomQueueLists: PropTypes.func.isRequired
}
RoomsQueueHistory.defaultProps = {
    _GetMyRoomQueueLists: (params = {}) => {
        console.log({params})
    }
}

export default connect(({Room}) => {
    let {queue} = Room
    let {all} = queue
    return {...all}
}, {_GetMyRoomQueueLists})(React.memo(RoomsQueueHistory))